  display: flex;
  flex-direction: column;
}

nav {
  background-color: var(--secondary-accent-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  height: 60px;
  
  
  
  button {
    background-color: transparent;
    border: none;
    color: white;
    font-size: 13pt;
    cursor: pointer;
    height: 60px;
    width: 90px;
    padding: var(--zero-pixels);
    margin: var(--zero-pixels);
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

#nav-left {
  display: flex;
  justify-content: flex-start;
  margin-left: 30px;
  margin-right: 30px;
  width: 50vw;
}

#nav-right {
  display: flex;
  justify-content: flex-end;
  margin-left: 30px;
  margin-right: 30px;
  width: 50vw;
}

main {
  padding: var(--zero-pixels);
  margin: var(--zero-pixels);
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-evenly; */
  align-items: center;
}

