  background-color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  /* height: calc(50% - 150px); */
  /* height: 50vh; */
  /* : calc(100% - 150px); */
  width: 100%;
  padding: 0px 10px;
  text-wrap: wrap;
}

.dog-info-card {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 20vw;
  text-align: center;
  text-wrap: balance;
}


.dog-info-card-photo {
  border-radius: 50%;
  border: 4px solid var(--main-accent-color);
  height: var(--smaller-dog-photo-size);
  width: var(--smaller-dog-photo-size);
}

