  flex-wrap: nowrap;
  justify-content: space-between;
  width: 50vw;
  min-width: 300px;
  max-width: 600px;
  margin: 0px 10px;
}

#github-repo-link {
  text-decoration: underline;
}

.dog-icon {
  width: 50px;
